import React, {useState} from 'react'
import logo from '../../assets/tr-logo.png';
import './Header.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
  } from "react-router-dom";

function Header({page}) {
    console.log(page);
    return (
        <div className="header">
            <div className="header__logo">
                <img src={logo} alt="" />
            </div>
            {page === 'home' &&
            <div className="header__right">
                <h4 onClick={()=>window.location.reload()}><Link to='/register' style={{ color: '#000', textDecoration:'none'}}>Login/Register</Link></h4>
                {/* <h4 onClick={()=>window.location.reload()}><Link to='/register' style={{ color: '#000', textDecoration:'none'}}>Register</Link></h4> */}
            </div>
            }
            {page === 'register' &&
            <div className="header__right">
            <h4 onClick={()=>window.location.reload()}><Link to='/' style={{ color: '#000', textDecoration:'none'}}>Home</Link></h4>
            </div>
            }
        </div>
    )
}

export default Header
